@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600&display=swap);
html {
	box-sizing: border-box;
}

*, *:before, *:after {
	margin: 0;
	padding: 0;
	box-sizing: inherit;
}

body {
	margin: 0;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-family: 'Nunito Sans', sans-serif;
	width: 100vw;
	height: 100vh;
	overflow: hidden;
}

input, button {
	font-family: 'Nunito Sans', sans-serif;
}
body{display:grid;place-items:center}.app{width:100vw;height:100vh;display:flex}.sidebar{height:100%;width:35%;max-width:400px}.chat{flex:1 1;height:100%}.sidebar{background:#fff}.sidebar .conversations-list{height:100%}.sidebar .conversations-list .conversation{cursor:pointer;-webkit-user-select:none;-ms-user-select:none;user-select:none;-webkit-appearance:none;-moz-appearance:none;appearance:none;background:transparent;border:0;display:block;width:100%;text-align:left;padding:20px 14px;position:relative;border-bottom:1px solid #EBEAEB}.sidebar .conversations-list .conversation:hover,.sidebar .conversations-list .conversation:focus{background:rgba(235,234,235,0.5);border-bottom-color:#011041}.sidebar .conversations-list .conversation.active{background:#EBEAEB}.sidebar .conversations-list .conversation:last-child{border-bottom:0}.sidebar .conversations-list .conversation h2{font-size:1rem}.sidebar .conversations-list .conversation p{font-size:.875rem;line-height:1rem}.sidebar .conversations-list .conversation .time{position:absolute;top:14px;right:14px;font-size:.75rem}.chat{display:flex;flex-direction:column;background:#F5FBFF}.chat .no-conversation-indicator{display:grid;place-items:center;width:100%;height:100%}.chat .messages{flex:1 1;padding:10px;height:calc(100% - 80px)}.chat .messages .simplebar-content{display:flex;flex-direction:column;justify-content:flex-end;min-height:100%}.chat .messages .simplebar-content .message{margin:10px;padding:14px 20px 20px;background:#fff}.chat .messages .simplebar-content .message .time{font-weight:bold;margin-bottom:8px;font-size:.75rem;color:#011041}.chat .messages .simplebar-content .message p{font-size:.9375rem}.chat .input-wrapper{flex:0 0 80px;background:#EBEAEB;padding:20px}.chat .input-wrapper form{display:flex;align-items:center;justify-content:space-between}.chat .input-wrapper form input{width:80%;height:50px;padding:0 12px;outline:0;border:none}.chat .input-wrapper form input:focus{border:1px solid #011041}.chat .input-wrapper form button{padding:14px 30px;background:#011041;-webkit-appearance:none;-moz-appearance:none;appearance:none;border:none;color:#fff}

